import Rete from 'rete'

export const APP_ID = "ariadne@0.2.0"

export const actionSocket = new Rete.Socket("Action");
export var dataSocket = new Rete.Socket("Data");

export var eventHandlers = {
  list: [],
  remove() {
    this.list.forEach((h) => {
      document.removeEventListener("keydown", h);
    });
    this.list = [];
  },
  add(name, h) {
    document.addEventListener(name, h, false);
    this.list.push(h);
  },
};
