import Rete from "rete";
import { MessageControl } from "./MessageControl";
import { actionSocket } from "../utils/common";

export class ProgressNode extends Rete.Component {
  constructor() {
    super("Progress");
    this.task = {
      outputs: {},
    };
  }

  builder(node) {
    var ctrl = new MessageControl(this.editor, node.data.msg);

    node.addControl(ctrl);
    node.addInput(new Rete.Input("act", "Depends on", actionSocket, true));
    node.addOutput(new Rete.Output("act", "Motivated by", actionSocket, true));
  }

  worker(node, inputs) {}
}