import Rete from "rete";
import * as AlightRenderPlugin from "rete-alight-render-plugin";
import * as ConnectionPlugin from "rete-connection-plugin";
import * as ContextMenuPlugin from "rete-context-menu-plugin";
import sample_data from "./sample-data.json"

import {TaskNode} from './components/TaskNode'
import {ProgressNode} from './components/ProgressNode'
import { APP_ID } from "./utils/common";

var components = [
  new TaskNode(),
  new ProgressNode()
];
var container = document.querySelector("#rete");

var editor = new Rete.NodeEditor(APP_ID, container);
editor.use(AlightRenderPlugin);
editor.use(ConnectionPlugin);
editor.use(ContextMenuPlugin);

var engine = new Rete.Engine(APP_ID);

components.map((c) => {
  editor.register(c);
  engine.register(c);
});

editor.on(
  "process connectioncreate connectionremove nodecreate noderemove",
  () => {
    compile();
    if (editor.silent) return;
    var jsonData = editor.toJSON();
    localStorage.setItem("ariadne", JSON.stringify(jsonData));
  }
);

var saveBtn = document.getElementById("save-btn");
saveBtn.onclick = () => {
  localStorage.setItem("ariadne", JSON.stringify(editor.toJSON()));
};

var importBtn = document.getElementById("import-btn");
importBtn.onchange = (e) => {
  var file = e.target.files[0];
  if (!file) {
    return;
  }
  var reader = new FileReader();
  reader.onload = function (e) {
    var contents = e.target.result;
    editor.fromJSON(JSON.parse(contents)).then(() => {
      editor.view.resize();
      compile();
    });
  };
  reader.readAsText(file);
};

var exportBtn = document.getElementById("export-btn");

exportBtn.onclick = () => {
  var file = new Blob([JSON.stringify(editor.toJSON())], {
    type: "application/json",
  });
  var a = document.createElement("a"),
    url = URL.createObjectURL(file);
  a.href = url;
  a.download = "data.json";
  document.body.appendChild(a);
  a.click();
  setTimeout(function () {
    document.body.removeChild(a);
    window.URL.revokeObjectURL(url);
  }, 0);
};

async function compile() {
  await engine.abort();
  await engine.process(editor.toJSON());
}

var data = JSON.parse(localStorage.getItem("ariadne"));
if (!data) {
  data = sample_data;
}

editor.fromJSON(data).then(() => {
  editor.view.resize();
  compile();
});
